/***** 3. Navigation *****/

/***** 3.a Standard navigation *****/

.dropdown-menu {
    /*background-color: #e2e3c8;*/
    border: none;
    display: none;
    float: left;
    left: 0;
    list-style: none;
    margin: 16px 0 0;
    min-width: 160px;
    padding: 5px 0;
    position: absolute;
    top: 100%;
    z-index: 1000;
}

.dropdown-menu > li {
    margin-top: 5px;
    margin-bottom: 5px;
}

.dropdown-menu > li > a {
    font-size: 16px;
    font-weight: 300;
    text-align: center;
    text-transform: none;
}

.dropdown-menu > li > a:hover,
.dropdown-menu > li > a:focus,
.dropdown-submenu:hover > a,
.dropdown-submenu:focus > a,
.dropdown-menu > .active > a:hover {
    /*background-color: #d8d9bf;*/
    background-image: none;
    color: black;
    filter: none;
    text-decoration: none;
}

.dropdown-menu > .active > a,
.dropdown-menu > .active > a:focus {
    color: inherit;
    background-color: inherit;
    background-image: inherit;
    background-repeat: inherit;
    filter: inherit;
    outline: inherit;
    text-decoration: none;
}

.nav {
    margin-bottom: 0;
}

.nav > li > a:hover,
.nav > li > a:focus {
    background-color: inherit;
}

.nav > li.dropdown.open.active > a:hover,
.nav > li.dropdown.open.active > a:focus {
    background-color: inherit;
    border-color: inherit;
    color: inherit;
}

.ww-nav {
    padding-top: 6px;
}

.ww-nav a {
    /*color:#494e55;*/
    font-family: 'Open Sans', sans-serif;
    font-size: 20px;
    font-weight: 300;
    line-height: 14px;
    margin-left: 0;
    margin-right: 2px;
    padding-right: 0;
    text-transform: uppercase;
    text-decoration: none;
}

.ww-nav a:hover {
    /*color:#9b7188;*/
}

.ww-nav > li {
    float: left;
    margin-left: 15px;
}

.ww-nav > li > a {
    padding: 25px;
}

.ww-nav > li.last a {
    padding-right: 0;
}

.zone-navigation {
    display: inline;
}

/***** 3.b Tinynav.js *****/

/* Desktop */
.tinynav {
    /*background-color:#f6f6ed; */
